import React, { PureComponent } from 'react'
import classnames from 'classnames'
import Sidebar from './components/menu/vertical-menu/Sidebar'
import Navbar from './components/navbar/Navbar'
import Footer from './components/footer/Footer'
import 'bootstrap/dist/css/bootstrap.min.css'
import MyNav from '../../src/MyNav'

import { connect } from 'react-redux'
import { collapseSidebar } from '../redux/actions/customizer/index'
let user1 = JSON.parse(localStorage.getItem('logInUserData'))

class VerticalLayout extends PureComponent {
  state = {
    width: window.innerWidth,
    sidebarState: true,
    layout: this.props.app?.customizer?.theme,
    collapsedContent: true,
    sidebarHidden: user1 ? false : true,
    currentLang: 'en',
    appOverlay: false,
    currRoute: this.props.location.pathname
  }
  collapsedPaths = []
  mounted = false
  updateWidth = () => {
    if (this.mounted) {
      this.setState((prevState) => ({
        width: window.innerWidth
      }))
    }
  }

  componentDidMount() {
    this.mounted = true
    let {
      location: { pathname },
      app: {
        customizer: { theme, direction }
      }
    } = this.props

    if (this.mounted) {
      if (window !== 'undefined') {
        window.addEventListener('resize', this.updateWidth, false)
      }
      if (this.collapsedPaths.includes(pathname)) {
        this.props.collapseSidebar(true)
      }

      let layout = theme
      let dir = direction
      if (dir === 'rtl')
        document.getElementsByTagName('html')[0].setAttribute('dir', 'rtl')
      else document.getElementsByTagName('html')[0].setAttribute('dir', 'ltr')
      return layout === 'dark'
        ? document.body.classList.add('dark-layout')
        : layout === 'semi-dark'
        ? document.body.classList.add('semi-dark-layout')
        : null
    }
  }

  componentDidUpdate(prevProps, prevState) {
    let {
      app: {
        customizer: { theme }
      }
    } = this.props

    let layout = theme
    if (this.mounted) {
      if (layout === 'dark') {
        document.body.classList.remove('semi-dark-layout')
        document.body.classList.add('dark-layout')
      }
      if (layout === 'semi-dark') {
        document.body.classList.remove('dark-layout')
        document.body.classList.add('semi-dark-layout')
      }
      if (layout !== 'dark' && layout !== 'semi-dark') {
        document.body.classList.remove('dark-layout', 'semi-dark-layout')
      }
    }
  }

  handleCollapsedMenuPaths = (item) => {
    let collapsedPaths = this.collapsedPaths
    if (!collapsedPaths.includes(item)) {
      collapsedPaths.push(item)
      this.collapsedPaths = collapsedPaths
    }
  }

  toggleSidebarMenu = (val) => {
    this.setState({
      sidebarState: !this.state.sidebarState,
      collapsedContent: !this.state.collapsedContent
    })
  }

  sidebarMenuHover = (val) => {
    this.setState({
      sidebarState: val
    })
  }

  handleSidebarVisibility = () => {
    if (this.mounted) {
      if (window !== undefined) {
        window.addEventListener('resize', () => {
          if (this.state.sidebarHidden) {
            this.setState({
              sidebarHidden: !this.state.sidebarHidden
            })
          }
        })
      }
      this.setState({
        sidebarHidden: !this.state.sidebarHidden
      })
    }
  }

  componentWillUnmount() {
    this.mounted = false
  }

  handleCurrentLanguage = (lang) => {
    this.setState({
      currentLang: lang
    })
  }

  handleAppOverlay = (value) => {
    if (value.length > 0) {
      this.setState({
        appOverlay: true
      })
    } else if (value.length < 0 || value === '') {
      this.setState({
        appOverlay: false
      })
    }
  }

  handleAppOverlayClick = () => {
    this.setState({
      appOverlay: false
    })
  }

  render() {
    const url = window.location.href.split('/#/#')[1]
    let appProps = this.props.app.customizer
    let menuThemeArr = [
      'primary',
      'success',
      'danger',
      'info',
      'warning',
      'dark'
    ]
    let sidebarProps = {
      toggleSidebarMenu: this.props.collapseSidebar,
      toggle: this.toggleSidebarMenu,
      sidebarState: this.state.sidebarState,
      sidebarHover: this.sidebarMenuHover,
      sidebarVisibility: this.handleSidebarVisibility,
      visibilityState: this.state.sidebarHidden,
      activePath: this.props.match.path,
      collapsedMenuPaths: this.handleCollapsedMenuPaths,
      currentLang: this.state.currentLang,
      activeTheme: appProps.menuTheme,
      collapsed: this.state.collapsedContent,
      permission: this.props.permission,
      deviceWidth: this.state.width
    }
    let navbarProps = {
      toggleSidebarMenu: this.toggleSidebarMenu,
      sidebarState: this.state.sidebarState,
      sidebarVisibility: this.handleSidebarVisibility,
      currentLang: this.state.currentLang,
      changeCurrentLang: this.handleCurrentLanguage,
      handleAppOverlay: this.handleAppOverlay,
      appOverlayState: this.state.appOverlay,
      navbarColor: appProps.navbarColor,
      navbarType: appProps.navbarType
    }

    let footerProps = {
      footerType: appProps.footerType,
      hideScrollToTop: appProps.hideScrollToTop
    }

    let user1 = localStorage.getItem('logInUserData')
    return (
      <>
        {window.screen.width <= 500 && (
          <MyNav
            toggle={this.handleSidebarVisibility}
            CurrentLanguage={this.handleCurrentLanguage}
          />
        )}
        <div
          className={classnames(
            `wrapper vertical-layout theme-${appProps.menuTheme}`,
            {
              'menu-collapsed':
                this.state.collapsedContent === true &&
                this.state.width >= 1200,
              'fixed-footer': appProps.footerType === 'sticky',
              'navbar-static': appProps.navbarType === 'static',
              'navbar-sticky': appProps.navbarType === 'sticky',
              'navbar-floating': appProps.navbarType === 'floating',
              'navbar-hidden': appProps.navbarType === 'hidden',
              'theme-primary': !menuThemeArr.includes(appProps.menuTheme)
            }
          )}
        >
          <Sidebar {...sidebarProps} />
          <div
            className={classnames(user1 ? 'app-content content' : 'margining', {
              'show-overlay': this.state.appOverlay === true
            })}
            onClick={this.handleAppOverlayClick}
          >
            <Navbar
              {...navbarProps}
              collapsedContent={this.state.collapsedContent}
            />
            <div
              className="content-wrapper"
              style={{
                padding:
                  window.innerWidth > 500
                    ? '0px 2.2rem'
                    : window.innerWidth <= 500 && url !== '/dashboard'
                    ? '1rem 1rem 5rem'
                    : '0px'
              }}
            >
              {this.props.children}
            </div>
          </div>

          <Footer {...footerProps} />

          <div
            className="sidenav-overlay"
            onClick={this.handleSidebarVisibility}
          />
        </div>
      </>
    )
  }
}
const mapStateToProps = (state) => {
  return {
    app: state.customizer
  }
}
export default connect(mapStateToProps, {
  collapseSidebar
})(VerticalLayout)
