import React from 'react'
import { CardBody, Form, FormGroup, Input, Label, Button } from 'reactstrap'
import { Spinner } from 'reactstrap'
import { encryptdata } from '../../utility/context/SecurityTool'
import 'swiper/css/swiper.css'
import '../../assets/scss/plugins/extensions/swiper.scss'
import crypto from 'crypto'
import Checkbox from '../../components/@vuexy/checkbox/CheckboxesVuexy'
import { Check } from 'react-feather'
import '../../assets/scss/pages/authentication.scss'
import axios from 'axios'
import './reg.css'
axios.defaults.baseURL = 'https://lastarzi2.herokuapp.com'
//axios.defaults.baseURL = "http://localhost:5000";

class Register extends React.Component {
  state = {
    activeTab: '1',
    next: false,
    accept: false,
    disbaleRegister: false,
    successRegister: false,
    errMsg: '',
    isLoading: false
  }
  toggle = (tab) => {
    if (this.state.activeTab !== tab) {
      this.setState({
        activeTab: tab
      })
    }
  }
  callnext = (e) => {
    if (this.state.confirmPass === this.state.password) {
      this.setState({ next: !this.state.next })
    } else {
      this.setState({ errMsg: 'Passwords do not match' })
      e.preventDefault()
    }
  }

  hashPassword2 = async (password) => {
    //const salt = await bcrypt.genSalt(6);
    //const salt = await crypto.randomBytes(16).toString('hex');
    const salt = '0e09ca7a4644c68c6c002ed1338bb40b'
    //alert(salt);
    const hash = await crypto
      .pbkdf2Sync(password, salt, 1000, 20, `sha512`)
      .toString(`hex`)
    //const hashed = await bcrypt.hash(password, salt);
    //alert(hash);
    return hash
  }

  hashPassword = (password) => {
    return new Promise((accept, reject) => {
      const PASSWORD_LENGTH = 256
      const SALT_LENGTH = 64
      const ITERATIONS = 10000
      const DIGEST = 'sha256'
      const BYTE_TO_STRING_ENCODING = 'hex' // this could be base64, for instance
      var salt = crypto
        .randomBytes(SALT_LENGTH)
        .toString(BYTE_TO_STRING_ENCODING)

      //var salt = bcrypt.genSaltSync(10);
      //var hash = bcrypt.hashSync(password, salt);
      var hash = crypto.pbkdf2(
        password,
        salt,
        ITERATIONS,
        PASSWORD_LENGTH,
        DIGEST,
        (error, hash) => {
          if (error) {
            reject(error)
          } else {
            accept({
              salt,
              hash: hash.toString(BYTE_TO_STRING_ENCODING),
              iterations: ITERATIONS
            })
          }
        }
      )
      //var hash = crypto.pbkdf2(password, salt, iterations);
      return hash
    })
  }

  handleRegister = (e) => {
    this.setState({ disbaleRegister: true, isLoading: true })
    e.preventDefault()
    this.hashPassword2(this.state.password).then((encpassword) => {
      //       var mykey = crypto.createCipher('aes-128-cbc', 'l@st@rz!');
      // var mystr = mykey.update(encpassword, 'utf8', 'hex')
      //   mystr += mykey.final('hex');
      // alert(encpassword);
      //      var diffHell = crypto.createDiffieHellman(60);
      // diffHell.generateKeys('hex');
      // const pubKey = diffHell.getPublicKey('hex');
      // const priKey = diffHell.getPrivateKey('hex');

      const {
        secretQustion1,
        secretQustion2,
        secretQustion3,
        secretAnswer1,
        secretAnswer2,
        secretAnswer3
      } = this.state
      e.preventDefault()
      var mykey1 = crypto.createCipher('aes-128-cbc', 'l@st@rz!')
      var mystr1 = mykey1.update(secretAnswer1, 'utf8', 'hex')
      mystr1 += mykey1.final('hex')

      var mykey2 = crypto.createCipher('aes-128-cbc', 'l@st@rz!')
      var mystr2 = mykey2.update(secretAnswer2, 'utf8', 'hex')
      mystr2 += mykey2.final('hex')

      var mykey3 = crypto.createCipher('aes-128-cbc', 'l@st@rz!')
      var mystr3 = mykey3.update(secretAnswer3, 'utf8', 'hex')
      mystr3 += mykey3.final('hex')

      // var mykey = crypto.createDecipher('aes-128-cbc', 'mypassword');
      // var mystr = mykey.update('34feb914c099df25794bf9ccb85bea72', 'hex', 'utf8')
      // mystr += mykey.final('utf8');
      const user = {
        //publickey: pubKey,
        salutation: this.state.salutation,
        email: this.state.email,
        name: encryptdata(this.state.name, false, encpassword),
        username: this.state.email,
        mobile: encryptdata(this.state.mobile, false, encpassword),
        password: encpassword,
        status: 'emailNotConfirmed',
        secrets: {
          secretQustion1,
          secretQustion2,
          secretQustion3,
          secretAnswer1: mystr1,
          secretAnswer2: mystr2,
          secretAnswer3: mystr3
        }
      }
      axios
        .post('/backendapi/saveuser', user)
        .then((res) => {
          this.setState({
            successRegister: true,
            isLoading: false
          })
        })
        .catch((e) => {
          this.setState({
            errMsg: 'Something went wrong. Please try after sometime!!'
          })
        })
    })
  }
  render() {
    return (
      <>
        <p className="px-2 auth-title mb-0" style={{ color: 'red' }}>
          {this.state.errMsg}
        </p>
        {this.state.successRegister && (
          <p className="px-2 auth-title mb-0" style={{ color: 'green' }}>
            {' '}
            A confirmation email has been sent to your email. Please confirm
            your email. If already confirmed{' '}
          </p>
        )}
        <CardBody className="pt-1">
          {!this.state.next && (
            <div>
              <Form action="/" onSubmit={this.callnext}>
                {/* <FormGroup className="form-label-group">
                  <CreatableSelect
                    isMulti={false}
                    isClearable={false}
                    options={prefixOptions}
                    className="React"
                    classNamePrefix="select"
                    defaultValue={prefixOptions[0]}
                    onChange={(e) =>
                      this.setState({
                        salutation: e && e.value ? e.value : null
                      })
                    }
                  />
                </FormGroup> */}
                <FormGroup className="form-label-group">
                  <Input
                    type="text"
                    placeholder="Name"
                    required
                    value={this.state.name}
                    onChange={(e) => this.setState({ name: e.target.value })}
                  />
                  <Label>Full Name</Label>
                </FormGroup>
                <FormGroup className="form-label-group">
                  <Input
                    type="email"
                    placeholder="Email"
                    required
                    value={this.state.email}
                    onChange={(e) => this.setState({ email: e.target.value })}
                  />
                  <Label>Email</Label>
                </FormGroup>
                <FormGroup className="form-label-group">
                  <Input
                    type="number"
                    placeholder="Phone Number"
                    required
                    value={this.state.mobile}
                    onChange={(e) => this.setState({ mobile: e.target.value })}
                  />
                  <Label>Phone Number</Label>
                </FormGroup>
                <FormGroup className="form-label-group">
                  <Input
                    type="password"
                    placeholder="Password"
                    required
                    value={this.state.password}
                    onChange={(e) =>
                      this.setState({ password: e.target.value })
                    }
                  />
                  <Label>Password</Label>
                </FormGroup>
                <FormGroup className="form-label-group">
                  <Input
                    type="password"
                    placeholder="Confirm Password"
                    required
                    value={this.state.confirmPass}
                    onChange={(e) =>
                      this.setState({
                        confirmPass: e.target.value
                      })
                    }
                  />
                  <Label>Confirm Password</Label>
                </FormGroup>

                <div className="d-flex justify-content-center">
                  <Button.Ripple color="primary" type="submit">
                    Next
                  </Button.Ripple>
                </div>
              </Form>
            </div>
          )}
          {this.state.next && (
            <div>
              <Form action="/" onSubmit={this.handleRegister}>
                <FormGroup className="form-label-group">
                  <Input
                    type="text"
                    placeholder="Secret Question 1"
                    required
                    value={this.state.secretQustion1}
                    onChange={(e) =>
                      this.setState({
                        secretQustion1: e.target.value
                      })
                    }
                  />
                  <Label>Secret Question 1</Label>
                </FormGroup>
                <FormGroup className="form-label-group">
                  <Input
                    type="password"
                    placeholder="Secret Answer 1"
                    required
                    value={this.state.secretAnswer1}
                    onChange={(e) =>
                      this.setState({
                        secretAnswer1: e.target.value
                      })
                    }
                  />
                  <Label>Secret Answer 1</Label>
                </FormGroup>
                <FormGroup className="form-label-group">
                  <Input
                    type="text"
                    placeholder="Secret Question 2"
                    required
                    value={this.state.secretQustion2}
                    onChange={(e) =>
                      this.setState({
                        secretQustion2: e.target.value
                      })
                    }
                  />
                  <Label>Secret Question 2</Label>
                </FormGroup>
                <FormGroup className="form-label-group">
                  <Input
                    type="password"
                    placeholder="Secret Answer 2"
                    required
                    value={this.state.secretAnswer2}
                    onChange={(e) =>
                      this.setState({
                        secretAnswer2: e.target.value
                      })
                    }
                  />
                  <Label>Secret Answer 2</Label>
                </FormGroup>
                <FormGroup className="form-label-group">
                  <Input
                    type="text"
                    placeholder="Secret Question 3"
                    required
                    value={this.state.secretQustion3}
                    onChange={(e) =>
                      this.setState({
                        secretQustion3: e.target.value
                      })
                    }
                  />
                  <Label>Secret Question 3</Label>
                </FormGroup>
                <FormGroup className="form-label-group">
                  <Input
                    type="password"
                    placeholder="Secret Answer 3"
                    required
                    value={this.state.secretAnswer3}
                    onChange={(e) =>
                      this.setState({
                        secretAnswer3: e.target.value
                      })
                    }
                  />
                  <Label>Secret Answer 3</Label>
                </FormGroup>
                <FormGroup>
                  <Checkbox
                    color="primary"
                    icon={<Check className="vx-icon" size={16} />}
                    label=" I accept the terms & conditions."
                    value={this.state.accept}
                    onChange={() => {
                      this.setState({
                        accept: !this.state.accept
                      })
                    }}
                  />
                </FormGroup>
                <div className="d-flex justify-content-between">
                  <Button.Ripple
                    color="primary"
                    outline
                    onClick={() => {
                      this.setState({ next: false })
                    }}
                  >
                    Back
                  </Button.Ripple>

                  <Button.Ripple
                    color={
                      this.state.disbaleRegister || !this.state.accept
                        ? 'primary'
                        : 'success'
                    }
                    type="submit"
                    disabled={this.state.disbaleRegister || !this.state.accept}
                  >
                    {this.state.isLoading && (
                      <Spinner color="danger" size="sm" />
                    )}{' '}
                    {this.state.isLoading ? 'Registering...' : 'Register'}
                  </Button.Ripple>
                </div>
              </Form>
            </div>
          )}
        </CardBody>
      </>
    )
  }
}
export default Register
