import React from 'react'
import { IntlProvider } from 'react-intl'
//aaquib language
import messages_en from '../../assets/data/en.json'
import messages_de from '../../assets/data/spn.json'
import messages_fr from '../../assets/data/frn.json'
import messages_pt from '../../assets/data/grm.json'
import messages_in from '../../assets/data/hin.json'
import messages_pk from '../../assets/data/urd.json'
import messages_cn from '../../assets/data/chn.json'

const menu_messages = {
  en: messages_en,
  de: messages_de,
  fr: messages_fr,
  pt: messages_pt,
  in: messages_in,
  pk: messages_pk,
  cn: messages_cn
}

const Context = React.createContext()

class IntlProviderWrapper extends React.Component {
  state = {
    locale: 'en',
    messages: menu_messages['en']
  }

  render() {
    const { children } = this.props
    const { locale, messages } = this.state
    return (
      <Context.Provider
        value={{
          state: this.state,
          switchLanguage: (language) => {
            this.setState({
              locale: language,
              messages: menu_messages[language]
            })
          }
        }}
      >
        <IntlProvider
          key={locale}
          locale={locale}
          messages={messages}
          defaultLocale="en"
        >
          {children}
        </IntlProvider>
      </Context.Provider>
    )
  }
}

export { IntlProviderWrapper, Context as IntlContext }
