import React, { useEffect, useState } from 'react'
import { Navbar } from 'reactstrap'
import { connect } from 'react-redux'
import classnames from 'classnames'
import { useAuth0 } from '../../../authServices/auth0/auth0Service'
import {
  logoutWithJWT,
  logoutWithFirebase,
  setSection
} from '../../../redux/actions/auth/loginActions'
import { NavLink, useHistory } from 'react-router-dom'
import NavbarUser from './NavbarUser'
import avatar1 from '../../../assets/img/avatar/female.png'
import avatar2 from '../../../assets/img/avatar/male.png'
import themeConfig from '../../../configs/themeConfig'
import { useLocation } from 'react-router-dom'
import Logo1 from '../../../assets/img/logo/logo-mast-removebg-preview.png'
import Logo2 from '../../../assets/img/logo/logo-mast-removebg-preview.png'
import './loginModalDiv.scss'
import LoginNew from '../../../../src/views/Login/Login'
import Register from '../../../../src/views/Register/Register'
import '../../../assets/scss/pages/authentication.scss'
import { Modal, ModalBody } from 'reactstrap'
const theme = localStorage.getItem('theme')
let user1 = JSON.parse(localStorage.getItem('logInUserData'))

const UserName = (props) => {
  let username = user1 ? (user1.name ? user1.name : user1.username) : null
  return username
}
const ThemeNavbar = (props) => {
  const history = useHistory()
  const { user } = useAuth0()
  const location = useLocation()
  const [mobileTitle, setMobileTitle] = useState('Last Arzi')
  const [showLoginModal, setShowLoginModal] = useState(false)
  const [isSignInSelected, setIsSignInSelected] = useState(true)
  const [loggedIn, setloggedIn] = useState(false)
  const colorsArr = ['primary', 'danger', 'success', 'info', 'warning', 'dark']
  const navbarTypes = ['floating', 'static', 'sticky', 'hidden']
  // const { plan, why, feature } = props
  // const handleit = (data) => {
  //   props.setSection(data)
  // }

  const toggleLoginModal = (data) => {
    setShowLoginModal(!showLoginModal)
    data === 'SignIn' ? setIsSignInSelected(true) : setIsSignInSelected(false)
  }
  useEffect(() => {
    if (loggedIn) {
      toggleLoginModal()
      history.push('/dashboard')
      window.location.reload()
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [loggedIn])

  useEffect(() => {
    if (location?.pathname) {
      const path = location?.pathname
      switch (path) {
        case '/diary':
          setMobileTitle('Secret Diary')
          break
        case '/spends':
          setMobileTitle('Daily Spends')
          break
        case '/vault/password':
          setMobileTitle('Vault')
          break
        case '/portfolio':
          setMobileTitle('Portfolio')
          break
        case '/assets/add':
          setMobileTitle('Assets')
          break
        case '/liability/add':
          setMobileTitle('Liabilities')
          break
        case '/nominee/list':
          setMobileTitle('Nominees')
          break
        case '/vault/document':
          setMobileTitle('Documents')
          break
        case '/contactUs':
          setMobileTitle('Sender')
          break
        default:
          setMobileTitle('Last Arzi')
          break
      }
    }
  }, [location])

  return (
    <React.Fragment>
      <div className="content-overlay" />
      <div className="" />
      <Navbar
        style={{
          background:
            themeConfig.theme === 'dark' ? '#21212a' : 'rgba(255,255,255)'
        }}
        className={classnames(
          `${
            props.collapsedContent ? (user1 ? 'full-width' : '') : 'fixed-width'
          } header-navbar navbar-expand-lg navbar navbar-with-menu navbar-shadow`,
          {
            'navbar-light':
              props.navbarColor === 'default' ||
              !colorsArr.includes(props.navbarColor),
            'navbar-dark': colorsArr.includes(props.navbarColor),
            'bg-primary':
              props.navbarColor === 'primary' && props.navbarType !== 'static',
            'bg-danger':
              props.navbarColor === 'danger' && props.navbarType !== 'static',
            'bg-success':
              props.navbarColor === 'success' && props.navbarType !== 'static',
            'bg-info':
              props.navbarColor === 'info' && props.navbarType !== 'static',
            'bg-warning':
              props.navbarColor === 'warning' && props.navbarType !== 'static',
            'bg-dark':
              props.navbarColor === 'dark' && props.navbarType !== 'static',
            'd-none': props.navbarType === 'hidden' && !props.horizontal,
            'floating-nav':
              (props.navbarType === 'floating' && !props.horizontal) ||
              (!navbarTypes.includes(props.navbarType) && !props.horizontal),
            'navbar-static-top':
              props.navbarType === 'static' && !props.horizontal,
            'fixed-top': props.navbarType === 'sticky' || props.horizontal,
            scrolling: props.horizontal && props.scrolling
          }
        )}
      >
        <div className="navbar-wrapper">
          <div className="navbar-container content">
            <div
              className="navbar-collapse d-flex justify-content-between align-items-center"
              id="navbar-mobile"
            >
              <div className="bookmark-wrapper d-flex">
                {props.collapsedContent && window.screen.width > 500 && (
                  <NavLink to="/dashboard" className="navbar-brand d-flex">
                    {!user1 && (
                      <img
                        alt="logo"
                        style={{ marginRight: '25px' }}
                        className="brand-logo"
                        height="25"
                        src={theme === 'dark' ? Logo2 : Logo1}
                      />
                    )}
                    <h2
                      className="brand-text mb-0"
                      style={{
                        fontWeight: '600',
                        color:
                          themeConfig.theme === 'dark'
                            ? '#ebeefd !important'
                            : '#757488'
                      }}
                    >
                      {mobileTitle}
                    </h2>
                  </NavLink>
                )}
                {window.screen.width <= 500 && (
                  <div style={{ display: 'flex' }}>
                    <h3
                      className="brand-text mb-0 ml-50"
                      style={{
                        fontWeight: '600',
                        color: '#FF9F43'
                      }}
                    >
                      {mobileTitle}
                    </h3>
                  </div>
                )}
              </div>
              {props.horizontal ? (
                <div className="logo d-flex align-items-center">
                  <div className="brand-logo mr-50"></div>
                  <h2 className="text-primary brand-text mb-0">Last Arzi2</h2>
                </div>
              ) : null}
              {window.screen.width > 500 && (
                <NavbarUser
                  handleAppOverlay={props.handleAppOverlay}
                  changeCurrentLang={props.changeCurrentLang}
                  userName={<UserName userdata={user} {...props} />}
                  userImg={
                    JSON.parse(localStorage.getItem('logInUserData')) &&
                    JSON.parse(localStorage.getItem('logInUserData'))
                      .salutation === 'Mr'
                      ? avatar2
                      : avatar1
                  }
                  loggedInWith={
                    props.user !== undefined &&
                    props.user.login.values !== undefined
                      ? props.user.login.values.loggedInWith
                      : null
                  }
                  toggleLoginModal={toggleLoginModal}
                  loggedIn={loggedIn}
                  setloggedIn={setloggedIn}
                  {...props}
                />
              )}
              <Modal isOpen={showLoginModal} toggle={toggleLoginModal}>
                <ModalBody>
                  <div className="loginModalDiv_container">
                    <div className="loginModalDiv_head">
                      <h3
                        className={isSignInSelected ? 'text_underline' : ''}
                        onClick={() => setIsSignInSelected(true)}
                      >
                        SignIn
                      </h3>
                      <h3
                        className={!isSignInSelected ? 'text_underline' : ''}
                        onClick={() => setIsSignInSelected(false)}
                      >
                        SignUp
                      </h3>
                    </div>
                    {isSignInSelected ? (
                      <LoginNew
                        loggedIn={loggedIn}
                        setloggedIn={setloggedIn}
                        {...props}
                      />
                    ) : (
                      <Register
                        loggedIn={loggedIn}
                        setloggedIn={setloggedIn}
                        {...props}
                      />
                    )}
                  </div>
                </ModalBody>
              </Modal>
            </div>
          </div>
        </div>
      </Navbar>
    </React.Fragment>
  )
}

const mapStateToProps = (state) => {
  const user = state.auth

  return {
    user: user,
    plan: user?.login?.plan,
    why: user?.login?.why,
    feature: user?.login?.feature
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    setSection: (state) => dispatch(setSection(state)),
    logoutWithJWT: (state) => dispatch(logoutWithJWT(state)),
    logoutWithFirebase: (state) => dispatch(logoutWithFirebase(state)),
    useAuth0: (state) => dispatch(useAuth0(state))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(ThemeNavbar)
