import React, { useState } from 'react'
import { CardBody } from 'reactstrap'
import './login1.css'
import 'swiper/css/swiper.css'
import '../../assets/scss/plugins/extensions/swiper.scss'
import crypto from 'crypto'
import '../../assets/scss/pages/authentication.scss'
import 'firebase/messaging'
import axios from 'axios'
import { decryptdata } from '../../utility/context/SecurityTool'
import '../Register/reg.css'
axios.defaults.baseURL = 'https://lastarzi2.herokuapp.com'
// axios.defaults.baseURL = 'http://localhost:5000'

const Register = (props) => {
  const { setloggedIn } = props
  const [password, setpassword] = useState('')
  const [mobileNo, setmobileNo] = useState('')
  // const [isLoading, setisLoading] = useState(false)
  // const [isError, setisError] = useState(false)
  // const [responseData, setresponseData] = useState({})
  const [errorMsg, seterrorMsg] = useState('')
  // const [status, setstatus] = useState('')
  // const [token, settoken] = useState('')
  // const [showPasswordFlag, setshowPasswordFlag] = useState(true)
  // const [isSubmit, setisSubmit] = useState(false)
  const [loginText, setloginText] = useState('Sign In')
  const handleChangeUsername = (event) => {
    setmobileNo(event.target.value)
  }

  const handleChangePassword = (event) => {
    setpassword(event.target.value)
  }

  // const goToForgotPassword = () => {
  //   history.push('/forgotpassword')
  // }

  // const showPassword = () => {
  //   setshowPasswordFlag(true)
  // }

  // const hidePassword = () => {
  //   setshowPasswordFlag(false)
  // }

  const subscribe = (email) => {
    console.log('###############################start')
    try {
      axios
        .post(
          '/backendapi/subscribe',
          {
            token: localStorage.getItem('fcmtoken'),
            topic: email
          },
          {
            headers: {
              Authorization: 'Bearer ' + localStorage.getItem('authtoken')
            }
          }
        )
        .then((res) => {})
    } catch (e) {
      console.log('###############################star4', e)
    }
  }

  const hashPassword2 = async (password) => {
    //const salt = await bcrypt.genSalt(6);
    //const salt = await crypto.randomBytes(16).toString('hex');
    const salt = '0e09ca7a4644c68c6c002ed1338bb40b'
    //alert(salt);
    const hash = await crypto
      .pbkdf2Sync(password, salt, 1000, 20, `sha512`)
      .toString(`hex`)
    //const hashed = await bcrypt.hash(password, salt);
    //alert(hash);
    return hash
  }

  const handleSubmit = (e) => {
    seterrorMsg('')
    // setisSubmit(true)
    setloginText('Signing In...')
    if (mobileNo === '' || password === '') {
      seterrorMsg('Invalid Credentials !')
    } else {
      hashPassword2(password.trim()).then((encpassword) => {
        axios
          .post('/backendapi/user/login', {
            user: mobileNo.trim(),
            password: encpassword
          })
          .then((res) => {
            if (
              res.data.length > 0 &&
              res.data[0].status !== 'emailNotConfirmed'
            ) {
              const resp = res.data[0]

              localStorage.setItem('secretkey', encpassword)
              resp.name = decryptdata(resp.name)
              resp.mobile = decryptdata(resp.mobile)
              resp.dob = decryptdata(resp.dob)
              resp.secondarymobile = decryptdata(resp.secondarymobile)
              resp.address = decryptdata(resp.address)

              resp.assets =
                resp.assets.length > 0
                  ? JSON.parse(decryptdata(resp.assets[0]))
                  : []
              resp.liabilities =
                resp.liabilities.length > 0
                  ? JSON.parse(decryptdata(resp.liabilities[0]))
                  : []
              resp.docs = decryptdata(resp.docs)
              seterrorMsg('')
              console.log('################# login', resp.toString())
              var d = new Date(resp.attendanceDt)
              d.setHours(0, 0, 0, 0)

              var e = new Date()
              e.setHours(0, 0, 0, 0)
              console.log('################# login', d, e)
              // let flag = false
              if (d.getTime() === e.getTime()) {
                // flag = true
              }
              console.log('Aaquib sss', res.headers)
              localStorage.setItem('logInUserData', JSON.stringify(resp))
              localStorage.setItem('authtoken', res.headers.authtoken)

              localStorage.setItem('logInGreeting', true)
              localStorage.setItem('theme', resp.theme ? resp.theme : 'light')
              subscribe(resp.email)
              setloggedIn(true)
              // history.push('/dashboard')
              // window.location.reload()
            } else if (res.data[0].status === 'emailNotConfirmed') {
              console.log('here1')
              const userkey =
                props.routerProps && props.routerProps.match.params.userkey
              if (res.data[0].status === userkey) {
                const resp = res.data[0]
                seterrorMsg('')
                setloggedIn(true)
                console.log('################# login', resp.toString())
                d = new Date(resp.attendanceDt)
                d.setHours(0, 0, 0, 0)

                e = new Date()
                e.setHours(0, 0, 0, 0)
                console.log('################# login', d, e)
                // let flag = false
                if (d.getTime() === e.getTime()) {
                  // flag = true
                }
                localStorage.setItem('logInUserData', JSON.stringify(resp))
                localStorage.setItem('logInGreeting', true)
                subscribe(resp.email)
                setloggedIn(true)
                // history.push('/dashboard')
                axios
                  .post('/backendapi/update/login', res.data[0], {
                    headers: {
                      Authorization:
                        'Bearer ' + localStorage.getItem('authtoken')
                    }
                  })
                  .then((res) => {})
                window.location.reload()
              } else {
                seterrorMsg('Please confirm your email for log in !! ')
                setloginText()
              }
            } else {
              seterrorMsg('Inavlid Credentials')
              setloginText('Sign In')
            }
          })
          .catch((error) => {
            if (error.response && error.response.status === 401) {
              seterrorMsg('Inavlid Credentials')
              setloginText('Sign In')
            } else {
              seterrorMsg('Something went Wrong ! Contact Your admin')
              setloginText('Sign In')
            }
          })
      })
    }
  }

  // if (
  //   localStorage.getItem('logInUserData') !== '' &&
  //   localStorage.getItem('logInUserData') !== null
  // ) {
  //   var logInUserData = JSON.parse(localStorage.getItem('logInUserData'))
  //   if (logInUserData !== null) {
  //     history.push('/dashboard')
  //   }
  // }
  return (
    <CardBody className="pt-0 mobile-min-height">
      <div className="form">
        <h5 style={{ color: 'red' }}>{errorMsg}</h5>
        <div>
          <input
            style={{
              textAlign: 'center',
              border: '1px solid grey',
              borderRadius: '5.5px'
            }}
            type="text"
            name="user"
            placeholder="Username"
            onChange={handleChangeUsername}
          />
          <input
            style={{
              textAlign: 'center',
              border: '1px solid grey',
              borderRadius: '5.5px'
            }}
            type="password"
            name="password"
            placeholder="Password"
            onChange={handleChangePassword}
          />
          <br />
          <br />

          <button
            onClick={handleSubmit}
            style={{
              border: '1px solid grey',
              borderRadius: '5.5px'
            }}
          >
            {loginText}
          </button>
        </div>
      </div>
    </CardBody>
  )
}
export default Register
